.card {
	display: flex;
	flex-direction: column;
	align-items: center;

	width: 22%;
	cursor: pointer;

	a {
		color: black;
		text-decoration: none;
	}
}

.recipe-item {
	position: relative;
	height: 220px;
	width: 220px;
	background-position: center center;
	border-radius: 0.2rem;
	box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
}

.minutes {
	position: absolute;
	top: 16px;
	left: 16px;
	background-color: whitesmoke;
	padding: 0.3rem 0.5rem;
	border-radius: 0.5rem;
	font-size: 0.8rem;
	box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
}
h2 {
	text-align: center;
	padding-bottom: 2rem;
	font-size: 32px;
	color: #4a4a4a;
}

.recipe-title {
	margin-bottom: 4rem;
}

@media (max-width: 890px) {
	.card {
		width: 50%;
	}

	.fav-cuisines-item {
		width: 80px;
		height: 80px;
		h3 {
			font-size: 8px;
			letter-spacing: normal;
		}
	}
}

@media (max-width: 475px) {
	.card {
		width: 100%;
	}

	.fav-cuisines-item {
		width: 50px;
		height: 50px;
	}
}
