@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;700&display=swap);
html{font-family:"Quicksand"}

body{background-color:#f7f7f7;margin:0 auto}

.header{max-width:1280px;text-align:center;padding-top:2rem;margin:0 auto}.header h1{color:#4a4a4a;margin:3rem 0}

.search-bar{position:relative;width:50%;margin:0 auto;padding:1.2rem 1rem 1rem;border:none;box-shadow:1px 1px 8px rgba(0,0,0,0.1);border-radius:2rem}.icon-search{color:lightgray;position:absolute;top:20px;left:30px;font-size:1.5rem}.search-input{width:100%;margin:0 auto;border:none;background-color:transparent;padding:0.2rem;color:lightgray;font-size:1.2rem;width:80%}.search-input::-webkit-input-placeholder{padding-left:rem;color:lightgray;font-family:"Europa";font-size:1.2rem}.search-input:-ms-input-placeholder{padding-left:rem;color:lightgray;font-family:"Europa";font-size:1.2rem}.search-input::-ms-input-placeholder{padding-left:rem;color:lightgray;font-family:"Europa";font-size:1.2rem}.search-input::placeholder{padding-left:rem;color:lightgray;font-family:"Europa";font-size:1.2rem}.search-input:focus{outline:none}

.fav-cuisines-item{cursor:pointer;width:130px;height:130px;border-radius:50%;background-size:cover;background-position:center center;display:flex;align-items:center;justify-content:center;margin:1rem 0.3rem;box-shadow:inset 0 0 150px rgba(0,0,0,0.4);transition:opacity 400ms}.fav-cuisines-item:hover{opacity:0.4}.fav-cuisines-item h3{margin:0 auto;width:80%;font-size:0.8rem;font-weight:bold;color:#fff;text-transform:uppercase;letter-spacing:2px;text-align:center}

.fav-cuisines-slide{display:flex;justify-content:space-around}

.card{display:flex;flex-direction:column;align-items:center;width:22%;cursor:pointer}.card a{color:black;text-decoration:none}.recipe-item{position:relative;height:220px;width:220px;background-position:center center;border-radius:0.2rem;box-shadow:1px 1px 3px rgba(0,0,0,0.5)}.minutes{position:absolute;top:16px;left:16px;background-color:whitesmoke;padding:0.3rem 0.5rem;border-radius:0.5rem;font-size:0.8rem;box-shadow:1px 1px 3px rgba(0,0,0,0.5)}h2{text-align:center;padding-bottom:2rem;font-size:32px;color:#4a4a4a}.recipe-title{margin-bottom:4rem}@media (max-width: 890px){.card{width:50%}.fav-cuisines-item{width:80px;height:80px}.fav-cuisines-item h3{font-size:8px;letter-spacing:normal}}@media (max-width: 475px){.card{width:100%}.fav-cuisines-item{width:50px;height:50px}}

.body-content{padding:5em 0;max-width:945px;margin:0 auto}.recipes-container{display:flex;flex-wrap:wrap;margin:0 auto;justify-content:space-between}

